/**
 * Site header
 */
.site-header {
  padding: calc($spacing-unit / 2) 0;

  .wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.site-title {
  @include relative-font-size(1.625);
  line-height: $base-line-height * $base-font-size * 2.25;
  letter-spacing: -1px;
  margin-bottom: 0;

  &,
  &:visited {
    color: $grey-color-dark;
  }
}

.site-nav {
  .nav-trigger {
      display: none;
  }

  .menu-icon {
    display: none;
  }

  .page-link {
    color: $text-color;
    line-height: $base-line-height;

    // Gaps between nav items, but not on the last one
    &:not(:last-child) {
      margin-right: 2rem;
    }

    &.active {
      // font-weight: bold;
    }
  }

  @include media-query($on-palm) {
    background-color: $background-color;
    border-radius: 5px;

    label[for="nav-trigger"] {
      display: block;
      width: 36px;
      height: 36px;
      z-index: 2;
      cursor: pointer;
    }

    .menu-icon {
      display: block;
      float: right;
      width: 36px;
      height: 26px;
      line-height: 0;
      padding-top: 10px;
      text-align: center;

      > svg {
        fill: $grey-color-dark;
      }
    }

    input ~ .trigger {
      opacity: 0;
      transition: all 200ms;
      z-index: -1;
      transform: translateY(-0.5rem);
    }

    .trigger {
      position: absolute;
      left: 0;
      z-index: 1;
      padding: 3rem 0;
      width: 100%;
      background-color: $background-color;
      box-shadow: 0px 16px 16px 0 rgba(0,0,0,0.2);

      .page-link {
        text-align: center;
      }
    }

    input:checked ~ .trigger {
      display: block;
      opacity: 1;
      z-index: 1;
      transform: translateY(0);
    }

    .page-link {
      display: block;
      padding: 0.375rem 0;

      &:not(:last-child) {
        margin-right: 0;
      }
    }
  }
}



/**
 * Site footer
 */
.site-footer {
  border-top: 1px solid $grey-color-light;
  padding: 4rem 0;
}

.footer-heading {
  margin-bottom: $spacing-unit * 0.5;
}

.contact-list,
.social-media-list {
  list-style: none;
  margin-left: 0;
}

.social-media-list {
  margin-bottom: 0;
  display: flex;
  flex-direction: row;
  gap: 1rem;
  justify-content: end;
  
  @include media-query($on-palm) {
    justify-content: start;
    margin-top: 1rem;
  }

  .svg-icon {
    zoom: 1.5;
  }
}



/**
 * Page content
 */

.page-heading {
  @include relative-font-size(2);
}

.post-list-heading {
  @include relative-font-size(1.75);
}

.post-list {
  margin-left: 0;
  list-style: none;

  > li {
    margin-bottom: $spacing-unit;
  }
}

.post-meta {
  font-size: $small-font-size;
  color: $grey-color;
}

.post-link {
  display: block;
  @include relative-font-size(1.5);
}



/**
 * Posts
 */
.post-header {
  margin-bottom: 2em;
}

.post-title {
  font-size: 3rem;
  letter-spacing: -1px;
  line-height: 1.25;

  @include media-query($on-laptop) {
    font-size: 2.25rem;
  }
}

.post-content {
  margin-bottom: $spacing-unit;

  h2 {
    @include relative-font-size(2);

    @include media-query($on-laptop) {
      @include relative-font-size(1.75);
    }
  }

  h3 {
    @include relative-font-size(1.625);

    @include media-query($on-laptop) {
      @include relative-font-size(1.375);
    }
  }

  h4 {
    @include relative-font-size(1.25);

    @include media-query($on-laptop) {
      @include relative-font-size(1.125);
    }
  }
}
