@import "minima";

$space-below-figure: 1.5rem;

body {
  font-family: "Atkinson Hyperlegible", $base-font-family;
  font-size: 1.25em;
}

.page-content {
  margin: 8rem 0;

  @include media-query($on-palm) {
    margin: 2rem 0;
  }
}

.bottom-spacer {
  margin-bottom: 8rem;

  @include media-query($on-laptop) {
    margin-bottom: 4rem;
  }
}

.display1 {
  font-size: 3.5rem;
  line-height: 1.25;

  @include media-query($on-laptop) {
    font-size: 2.5rem;
  }
}

.lead {
  font-size: 1.125em;
  color: $grey-color-dark;
}

.pull-quote {
  float: right;
  min-width: 40%;
  margin-left: 2rem;
  margin-bottom: 2rem;
}

.big-quote {
  text-align: center;
  padding: 1rem 0;
  border-left: none;
  // border-top: 2px solid $grey-color-light;
  // border-bottom: 2px solid $grey-color-light;

  &:before, &:after {
    display: inline-block;
    width: 100px;
    height: 4px;
    background-color: $grey-color-light;
    content: '';
  }

  p {
    margin: 1rem 0;
  }
}

.columns {
  display: grid;
  gap: 2.5rem;
  grid-template-columns: 1fr 1fr;

  @include media-query($on-palm) {
    grid-template-columns: 1fr;
    gap: 0;
  }
}

figure {
  margin-bottom: $space-below-figure;

  .columns & {
    margin-bottom: 0;

    @include media-query($on-palm) {
        margin-bottom: $space-below-figure;

    }
  }

  .columns:has(&) {
    margin-bottom: $space-below-figure;
  }
}

aside {
  background-color: $grey-color-light;
  padding: 2rem;
  border-radius: 1rem;
  margin-bottom: 1rem;

  img {
    float: right;
    width: 5.5rem;
    margin-left: 2rem;
    margin-bottom: 2rem;
  }

  p:last-child {
    margin-bottom: 0;
  }
}

.portfolio_piece {
  h3 {
    margin-top: 1rem;
  }

  img {
    width: 100%;
  }

  a {
    color: $text-color;

    &:visited {
      color: lighten($text-color, 20%);
    }
  }
}

// PhotoSwipe
.photo-swipe {
  cursor: zoom-in;
}
.pswp__custom-caption {
  background: $grey-color-dark;
  font-size: 1rem;
  color: #fff;
  width: calc(100% - 2rem);
  max-width: 400px;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  position: absolute;
  left: 50%;
  bottom: 1rem;
  transform: translateX(-50%);
}
.pswp__custom-caption a {
  color: #fff;
  text-decoration: underline;
}
.hidden-caption-content {
  display: none;
}
